import ReactGA from 'react-ga';

// ReactGA.initialize(process.env.GATSBY_GA_TRACKING_ID);
ReactGA.initialize("UA-115782569-1");

const GA = {
  trackLinkEvent(label = '') {
    ReactGA.event({
      category: 'Link',
      action: 'click',
      label,
    });
  }
};

export default GA;



