import React, { Component } from 'react';
import AppBar from 'material-ui/AppBar';
import IconButton from 'material-ui/IconButton';
import ArrowBack from 'material-ui/svg-icons/navigation/arrow-back';
import './index.css'
import Link from "gatsby-link";

class NavbarFixed extends Component {
  onLeftIconButtonClick = () => {
    // this.context.router.goBack();
    // const { canGoBack } = this.props;

    // if (canGoBack) {
    // }
  }

  renderIconElementRight() {
    const { showBlogLink } = this.props;

    if (!showBlogLink) return null;

    return (
      <Link
        to="/blog/"
        style={{color: 'black', textDecoration: 'none'}}>
        BLOG
      </Link>
    );
  }

  renderIconElementLeft() {
    const { canGoBack, backToUrl } = this.props;

    if (!canGoBack) return null;

    return (
      <Link to={backToUrl}>
        <IconButton>
          <ArrowBack color="black" />
        </IconButton>
      </Link>
    );
  }

  render() {
    const { title = '' } = this.props;

    return (
      <div className="custom-nav">
        <AppBar
          title={title}
          iconElementLeft={this.renderIconElementLeft()}
          iconElementRight={this.renderIconElementRight()}
          onLeftIconButtonClick={this.onLeftIconButtonClick}
          iconStyleRight={{
            display: 'flex',
            alignItems: 'center',
            margin: 0,
            fontFamily: 'Raleway',
          }}
          titleStyle={{
            color: 'black',
            fontFamily: 'Raleway',
          }}
          style={{
            backgroundColor: 'white',
          }}
        />
      </div>
    );
  }
}

export default NavbarFixed;



